<template>
    <div>
        <ui-list bordered>
            <ui-listitem v-for="n in 3"
                         @click="onClick(n)"
                         :key="n">{{n}}</ui-listitem>
        </ui-list>
        <h3>{{msg}}</h3>
    </div>
</template>
<script>
export default {
    data () {
        return {
            msg: ''
        }
    },
    methods: {
        onClick (n) {
            this.msg = `${n}`
        }
    }
}
</script>



