<template>
    <div>
        <ui-edit-area :value="value"
                    isTextarea
                    :wrapable="false"
                    @change="change">
        </ui-edit-area>
        <button @click="value = '22222'">change</button>
        {{value}}
    </div>
</template>
<script>
export default {
    data() {
        return {
            value: ''
        }
    },
    methods: {
        change(val) {
            console.log(val)
        }
    }
}
</script>



