<template>
    <docs-page>
        <code-block title=""
                    :code="baseCode"
                    desc="`v-popover``arg``name`
`click`">
            <baseDemo></baseDemo>
        </code-block>
        <code-block title=""
                    :code="placementCode"
                    desc="12`placement`">
            <placementDemo></placementDemo>
        </code-block>
        <code-block title=""
                    :code="fullHeightCode"
                    desc="">
            <fullHeightDemo></fullHeightDemo>
        </code-block>
        <code-block title=""
                    :code="nestedCode"
                    desc="">
            <nestedDemo></nestedDemo>
        </code-block>
        <code-block title=""
                    :code="singletonCode"
                    desc="
> `popoverHolder`">
            <singletonDemo></singletonDemo>
        </code-block>
    </docs-page>
</template>
<script>
import baseDemo from './base'
import baseCode from '!raw-loader!./base'
import placementDemo from './placement'
import placementCode from '!raw-loader!./placement'
import fullHeightDemo from './fullHeight'
import fullHeightCode from '!raw-loader!./fullHeight'
import nestedDemo from './nested'
import nestedCode from '!raw-loader!./nested'
import singletonDemo from './singleton'
import singletonCode from '!raw-loader!./singleton'

export default {
    components: {
        baseDemo,
        placementDemo,
        fullHeightDemo,
        nestedDemo,
        singletonDemo
    },
    data() {
        return {
            baseCode,
            placementCode,
            fullHeightCode,
            nestedCode,
            singletonCode
        }
    },
}
</script>



