<template>
    <div class="demo-treelist">
        <ui-list>
            <ui-listitem to="/"></ui-listitem>
            <ui-listitem to="/icon"></ui-listitem>
            <ui-divider></ui-divider>
            <ui-sub-header></ui-sub-header>
            <ui-listitem :tree="treeRoutes">
                <template slot-scope="{ node }">
                    <i v-if="node.icon"
                       :class="`icon-${node.icon}`"></i> {{node.text}}
                </template>
            </ui-listitem>
        </ui-list>
    </div>
</template>
<script>
export default {
    data () {
        return {
            treeRoutes: {
                id: '000',
                text: '',
                icon: 'sitemap',
                children: [{
                    id: 111,
                    text: '',
                    icon: 'code',
                    children: [{
                        id: 1111,
                        text: ''
                    }, {
                        id: 2222,
                        text: ''
                    }, {
                        id: 3333,
                        text: ''
                    }, {
                        id: 4444,
                        text: ''
                    }]
                }, {
                    id: 222,
                    text: '',
                    icon: 'tasks',
                }, {
                    id: 333,
                    text: '',
                    icon: 'user-circle'
                }]
            }
        }
    }
}
</script>
<style>
.demo-treelist {
    max-width: 300px;
    border: 1px solid #ddd;
}
</style>



