<template>
    <docs-page>
        <code-block title=""
                    :code="simpleCode"
                    desc="`borderer`">
            <simpleDemo></simpleDemo>
        </code-block>
        <code-block title=""
                    :code="routerCode"
                    desc="`to``router-link``to`">
            <routerDemo></routerDemo>
        </code-block>
        <code-block title=""
                    :code="childrenCode">
            <childrenDemo></childrenDemo>
            <template slot="desc">
                <p>
                    <code>tree</code>
                    <code>id</code>
                    <code>key</code></p>
                <p></p>
                <p>
                    <code>Divider</code></p>
            </template>
        </code-block>
    </docs-page>
</template>
<script>
import simpleDemo from './simple'
import simpleCode from '!raw-loader!./simple'
import routerDemo from './router'
import routerCode from '!raw-loader!./router'
import childrenDemo from './children'
import childrenCode from '!raw-loader!./children'

export default {
    components: {
        simpleDemo,
        routerDemo,
        childrenDemo
    },
    data() {
        return {
            simpleCode,
            routerCode,
            childrenCode,
            tab: 'props'
        }
    }
}
</script>
<style lang="less">

</style>



