<template>
    <div>
        <!--  -->
        <ui-popover-holder></ui-popover-holder>
        <button v-popover.only="singlePop">Click</button>
        <button v-popover.only="singlePop2">Click2</button>
        <button v-popover.only="singlePop3">Click3</button>
        <h3>{{msg}}</h3>
    </div>
</template>
<script>
const pop = {
    template: `<h1>{{message}}</h1>`,
    props: {
        message: String
    }
}

const pop2 = {
    template: `<button @click="onClick">Click!</button>`,
    methods: {
        onClick () {
            this.$emit('click', '2')
        }
    }
}

const pop3 = {
    template: `<button @click="onClick"></button>`,
    methods: {
        onClick () {
            this.$popover.visible = false
            // this.$emit('close:popover')
        }
    }
}

export default {
    data () {
        return {
            msg: '',
            singlePop: {
                component: pop, //  popover 
                message: '1', // 
            },
            singlePop2: {
                component: pop2,
                events: { // 
                    click: msg => {
                        this.msg = msg
                    },
                },
                popover: { // 
                    placement: 'top',
                    events: { // 
                        hide: () => {
                            this.msg = ''
                        }
                    }
                },
            },
            singlePop3: {
                component: pop3
            }
        }
    }
}
</script>



