<template>
    <docs-page>
        <code-block title=""
                    :code="simpleCode">
            <simpleDemo></simpleDemo>
            <template slot="desc">
                <p>
                    <code>disabled</code></p>
                <p>
                    <code>left</code>
                    <code>center</code>
                    <code>right</code>
                    <code>fill</code></p>
            </template>
        </code-block>
    </docs-page>
</template>
<script>
import simpleDemo from './simple'
import simpleCode from '!raw-loader!./simple'

export default {
    components: {
        simpleDemo
    },
    data() {
        return {
            simpleCode
        }
    }
}
</script>
<style lang="less">

</style>



