<template>
    <docs-page>
        <code-block title=""
                    :code="simpleCode">
            <simpleDemo></simpleDemo>
            <div slot="desc"><p></p></div>
        </code-block>
        <code-block title=""
                    :code="labelCode"
                    desc="`align``left``center``right`">
            <labelDemo></labelDemo>
        </code-block>
    </docs-page>
</template>
<script>
import simpleDemo from './simple'
import labelDemo from './label'
import simpleCode from '!raw-loader!./simple'
import labelCode from '!raw-loader!./label'

export default {
    components: {
        simpleDemo,
        labelDemo
    },
    data () {
        return {
            simpleCode,
            labelCode
        }
    }
}
</script>



