        </ui-tab-panel>
        <ui-tab-panel name="tab2">
            <p></p>
        </ui-tab-panel>
        <ui-tab-panel name="tab3">
            <p></p>
        </ui-tab-panel>
    </ui-tabs>
</template>
<script>
export default {
    data () {
        return {
            activeTab: 'tab1'
        }
    }
}
</script>



