<template>
    <div>
        <ui-tabbar v-model="activeTab">
            <ui-tab name="tab1"></ui-tab>
            <ui-tab name="tab2"></ui-tab>
            <ui-tab name="tab3"></ui-tab>
        </ui-tabbar>
        <br>
        <ui-tabbar v-model="activeTab">
            <ui-tab name="tab1"></ui-tab>
            <ui-tab name="tab2"
                    disabled></ui-tab>
            <ui-tab name="tab3"></ui-tab>
        </ui-tabbar>
        <br>
        <ui-tabbar v-model="activeTab" align="center">
            <ui-tab name="tab1"></ui-tab>
            <ui-tab name="tab2"></ui-tab>
            <ui-tab name="tab3"></ui-tab>
        </ui-tabbar>
        <br>
        <ui-tabbar v-model="activeTab" fill>
            <ui-tab name="tab1"></ui-tab>
            <ui-tab name="tab2"></ui-tab>
            <ui-tab name="tab3"></ui-tab>
        </ui-tabbar>
    </div>
</template>
<script>
export default {
    data () {
        return {
            activeTab: 'tab1'
        }
    }
}
</script>



