<template>
    <docs-page>
        <code-block title=""
                    desc="`tooltip`"
                    :code="baseCode">
            <baseDemo></baseDemo>
        </code-block>
        <code-block title=""
                    desc=""
                    :code="themeCode">
            <themeDemo></themeDemo>
        </code-block>
    </docs-page>
</template>
<script>
import baseDemo from './base'
import baseCode from '!raw-loader!./base'
import themeDemo from './theme'
import themeCode from '!raw-loader!./theme'

export default {
    components: {
        baseDemo,
        themeDemo
    },
    data () {
        return {
            baseCode,
            themeCode
        }
    }
}
</script>



