<template>
    <docs-page>
        <code-block title=""
                    :code="simpleCode"
                    desc="">
            <simpleDemo></simpleDemo>
        </code-block>
        <code-block title=" Tabbar "
                    :code="tabbarCode"
                    desc="`ui-tabbar``tabbar`">
            <tabbarDemo></tabbarDemo>
        </code-block>
        <code-block title=""
                    :code="directionCode"
                    desc='`layout="x"`'>
            <directionDemo></directionDemo>
        </code-block>
    </docs-page>
</template>
<script>
import simpleDemo from './simple'
import simpleCode from '!raw-loader!./simple'
import tabbarDemo from './tabbar'
import tabbarCode from '!raw-loader!./tabbar'
import directionDemo from './direction'
import directionCode from '!raw-loader!./direction'

export default {
    components: {
        simpleDemo,
        tabbarDemo,
        directionDemo
    },
    data () {
        return {
            simpleCode,
            tabbarCode,
            directionCode
        }
    }
}
</script>



